import Vue from 'vue'
import Router from 'vue-router'
import iView from 'iview'
import auth from 'common/js/auth'

//
import Admin from '@/components/admin/index'
import AdminAnime from '@/components/admin/anime/index'
import AdminAnimeCreate from '@/components/admin/anime/create'
import AdminAnimeUpdate from '@/components/admin/anime/update'
import AdminArticle from '@/components/admin/article/index'
import AdminArticleCreate from '@/components/admin/article/create'
import AdminArticleUpdate from '@/components/admin/article/update'
import AdminTv from '@/components/admin/tv/index'
import AdminTvCreate from '@/components/admin/tv/create'
import AdminTvUpdate from '@/components/admin/tv/update'
import AdminPicture from '@/components/admin/picture/index'
import AdminPictureCreate from '@/components/admin/picture/create'
import AdminPictureUpdate from '@/components/admin/picture/update'
import AdminMovie from '@/components/admin/movie/index'
import AdminMovieCreate from '@/components/admin/movie/create'
import AdminMovieUpdate from '@/components/admin/movie/update'
import AdminLogin from '@/components/admin/login/index'



//
import HomeApp from '@/components/home/index'
import Home from '@/components/home/home/index'
import Look from '@/components/home/look/index'

import Anime from '@/components/home/anime/index'
import AnimeDetail from '@/components/home/anime/detail'
import AnimeShow from '@/components/home/anime/show'

import Movie from '@/components/home/movie/index'
import MovieShow from '@/components/home/movie/show'

import Picture from '@/components/home/picture/index'
import PictureShow from '@/components/home/picture/show'


import Tv from '@/components/home/tv/index'
import TvDetail from '@/components/home/tv/detail'
import TvShow from '@/components/home/tv/show'

import Article from '@/components/home/article/index'
import ArticleShow from '@/components/home/article/show'
import Login from '@/components/home/login/index'
import Register from '@/components/home/register/index'
import Search from '@/components/home/search/search'

import User from '@/components/home/user/index'
import UserFav from '@/components/home/user/fav'
import UserCommit from '@/components/home/user/commit'
import UserFan from '@/components/home/user/fan'
import UserInfo from '@/components/home/user/info'
import UserPassword from '@/components/home/user/password'
import UserThumb from '@/components/home/user/thumb'



Vue.use(Router);


const router =  new Router({
  routes: [
    {
      path: '/',
      name: '',
      redirect: '/home',
      component: HomeApp,
      hidden: true,
      children: [
        { path: '/home', component: Home, name: '',meta: { notKeepAlive: true }},
        { path: '/look', component: Look, name: '',},

        { path: '/anime', component: Anime, name: '',},
        { path: '/anime/detail/:id', component: AnimeDetail, name: '',meta: { notKeepAlive: true }},
        { path: '/anime/:id', component: AnimeShow, name: '',meta: { notKeepAlive: true }},

        { path: '/movie', component: Movie, name: ''},
        { path: '/movie/:id', component: MovieShow, name: '',meta: { notKeepAlive: true }},

        { path: '/picture', component: Picture, name: ''},
        { path: '/picture/:id', component: PictureShow, name: '',meta: { notKeepAlive: true }},

        { path: '/tv', component: Tv, name: '',},
        { path: '/tv/detail/:id', component: TvDetail, name: '',meta: { notKeepAlive: true }},
        { path: '/tv/:id', component: TvShow, name: '',meta: { notKeepAlive: true }},

        { path: '/article', component: Article, name: ''},
        { path: '/article/:id', component: ArticleShow, name: '',meta: { notKeepAlive: true }},
        { path: '/user/:id', component: User, name: '',meta: { notKeepAlive: true },
          children: [
            { path: 'fav/:type', component: UserFav, name: '',meta: { notKeepAlive: true }},
            { path: 'commit/:type', component: UserCommit, name: '',meta: { notKeepAlive: true }},
            { path: 'fan/:type', component: UserFan, name: '',meta: { notKeepAlive: true }},
            { path: 'info', component: UserInfo, name: '',meta: { notKeepAlive: true }},
            { path: 'password', component: UserPassword, name: '',meta: { notKeepAlive: true }},
            { path: 'thumb', component: UserThumb, name: '',meta: { notKeepAlive: true }},
          ]
        },
      ]
    },
    {
      path: '/admin/login',
      name: '',
      component: AdminLogin,
      hidden: true
    },
    {
      path: '/login',
      name: '',
      component: Login,
      hidden: true
    },
    {
      path: '/register',
      name: '',
      component: Register,
      hidden: true
    },
    {
      path: '/search',
      name: '',
      component: Search,
      hidden: true
    },
    {
      path: '/admin',
      name: '',
      component: Admin,
      meta: {
        requiresAuth: true
      },
      children: [
        { path: '/admin/anime', component: AdminAnime, name: '',index:'1-1',},
        { path: '/admin/anime/create', component: AdminAnimeCreate, name: '',index:'1-2',},
        { path: '/admin/anime/:id', component: AdminAnimeUpdate, name: '',index:'1-3', hidden: true ,meta: { notKeepAlive: true }},

      ]
    },
    {
      path: '/admin',
      name: '',
      component: Admin,
      meta: {
        requiresAuth: true
      },
      children: [
        { path: '/admin/tv', component: AdminTv, name: '',index:'3-1',},
        { path: '/admin/tv/create', component: AdminTvCreate, name: '',index:'3-2',},
        { path: '/admin/tv/:id', component: AdminTvUpdate, name: '',index:'3-3', hidden: true ,meta: { notKeepAlive: true }},

      ]
    },
    {
      path: '/admin',
      name: '',
      component: Admin,
      meta: {
        requiresAuth: true
      },
      children: [
        { path: '/admin/movie', component: AdminMovie, name: '',index:'4-1',},
        { path: '/admin/movie/create', component: AdminMovieCreate, name: '',index:'4-2',},
        { path: '/admin/movie/:id', component: AdminMovieUpdate, name: '',index:'4-3', hidden: true ,meta: { notKeepAlive: true }},

      ]
    },
    {
      path: '/admin',
      name: '',
      component: Admin,
      meta: {
        requiresAuth: true
      },
      children: [
        { path: '/admin/article', component: AdminArticle, name: '',index:'2-1',},
        { path: '/admin/article/create', component: AdminArticleCreate, name: '',index:'2-2',meta: { notKeepAlive: true }},
        { path: '/admin/article/:id', component: AdminArticleUpdate, name: '',index:'2-3', hidden: true,meta: { notKeepAlive: true }},
      ]
    },
    {
      path: '/admin',
      name: '',
      component: Admin,
      meta: {
        requiresAuth: true
      },
      children: [
        { path: '/admin/picture', component: AdminPicture, name: '',index:'5-1',},
        { path: '/admin/picture/create', component: AdminPictureCreate, name: '',index:'5-2',meta: { notKeepAlive: true }},
        { path: '/admin/picture/:id', component: AdminPictureUpdate, name: '',index:'5-3', hidden: true,meta: { notKeepAlive: true }},
      ]
    },
  ]
})


router.beforeEach((to, from, next) => {
  iView.LoadingBar.start();
  if (to.matched.some(record => record.meta.requiresAuth)) {
    if (!auth.loggedIn() || JSON.parse(localStorage.getItem('kkzUser')).name !== 'xiaohai') {

      next({
        path: '/home'
      });
      next()
    } else {
      next()
    }
  } else {
    next()
  }

});

router.afterEach((to, from, next) => {
  iView.LoadingBar.finish()
});

export default router


