<template>
  <Form ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="80">

    <Form-item label="" prop="title">
      <Input v-model="formValidate.title" placeholder=""></Input>
    </Form-item>

    <Form-item label="" prop="thumb">

      <div class="demo-upload-list" v-if="formValidate.thumb">
        <img :src="formValidate.thumb">
        <div class="demo-upload-list-cover">
          <Icon type="ios-eye-outline" @click.native="handleView(formValidate.thumb)"></Icon>
          <Icon type="ios-trash-outline" @click.native="handleRemove(formValidate.thumb)"></Icon>
        </div>
      </div>




      <Upload
        ref="upload"
        :show-upload-list="false"
        :default-file-list="defaultList"
        :on-success="handleSuccess"
        :format="['jpg','jpeg','png']"
        :max-size="2048"
        :on-format-error="handleFormatError"
        :on-exceeded-size="handleMaxSize"
        
        type="drag"
        action="http://www.lifanh.com/api/image"
        :data="imgData"
        style="display: inline-block;width:80px">
        <div v-if="!formValidate.thumb" style="width: 80px;height:80px;line-height: 80px;border: 1px dashed #dddee1;">
          <Icon type="camera" size="20"></Icon>
        </div>
      </Upload>

      <Modal title="" v-model="visible">
        <img :src="imgName" v-if="visible" style="width: 100%">
      </Modal>

    </Form-item>



    <Form-item label="" prop="akira">
      <Select v-model="formValidate.akira" filterable multiple size="large">
        <Option v-for="item in akiraList" :value="item.name" :key="item.name">{{ item.name }}</Option>
      </Select>
    </Form-item>

    <Form-item label="" prop="tag">
      <Select v-model="formValidate.tag" filterable multiple size="large">
        <Option v-for="item in tagList" :value="item.name" :key="item.name">{{ item.name }}</Option>
      </Select>
    </Form-item>



    <Form-item label="" prop="issue_date">
      <Input v-model="formValidate.issue_date" placeholder=""></Input>
    </Form-item>


    <Form-item label="" prop="introduction">
      <Input v-model="formValidate.introduction" type="textarea" :autosize="{minRows: 2,maxRows: 5}" placeholder="..."></Input>
    </Form-item>


    <Form-item
      v-for="(item, index) in formValidate.files"
      :key="index"
      :label="'' + (index + 1)">

      <Row>
        <i-col span="9">
          <Input type="text" v-model="item.file_name" placeholder="...">
          <Icon type="document-text" slot="prepend"></Icon>
          </Input>
        </i-col>

        <i-col span="9" offset="1">
          <Input type="text" v-model="item.file_url" placeholder="Url,http://...">
          <Icon type="earth" slot="prepend"></Icon>
          </Input>
        </i-col>

        <i-col span="3" offset="1">
          <Button type="ghost" @click="filesRemove(index)"></Button>
        </i-col>
      </Row>
    </Form-item>


    <Form-item>
      <Row>
        <i-col span="12">
          <Button type="dashed" long @click="filesAdd" icon="plus-round"></Button>
        </i-col>
      </Row>
    </Form-item>

    <Form-item>
      <Button type="primary" @click="handleSubmit('formValidate')"></Button>
      <Button type="ghost" @click="handleReset('formValidate')" style="margin-left: 8px"></Button>
    </Form-item>
  </Form>
</template>
<script>
  import ICol from "../../../../node_modules/iview/src/components/grid/col";
  import {getAkiraList,getTagList, postUpdateAnimeList,getShowAnimeList} from 'api/anime';
  import Mock from 'mockjs';
  import {ERR_OK} from 'api/config';
  import {postImageDelete} from 'api/image'

  export default {
    components: {ICol},
    data () {
      return {
        //
        imgData: {
          width: 300,
          height: 300,
        },
        //
        defaultList: [

        ],
        imgName: '',
        visible: false,
        //
        tagList: [],
        akiraList: [],
        formValidate: {
          title: '',
          thumb: '',
          issue_date: '',
          akira: [],
          tag: [],
          introduction: '',
          files: [

            {
              file_name: '',
              file_url: ''
            }

          ],
        },
        ruleValidate: {
          title: [
            { required: true, message: '', trigger: 'blur' }
          ],
          files: [

          ],
          thumb: [
            { required: true, message: '', trigger: 'blur'}
          ],
          akira: [
            { required: true, type: 'array', min: 1, message: '', trigger: 'change' },
            { type: 'array', max: 4, message: '', trigger: 'change' }
          ],
          tag: [
            { required: true, type: 'array', min: 1, message: '', trigger: 'change' },
            { type: 'array', max: 4, message: '', trigger: 'change' }
          ],
          introduction: [
            { required: true, message: '', trigger: 'blur' },
            { type: 'string', min: 10, message: '10', trigger: 'blur' }
          ]
        }
      }
    },
    created() {
      this._getAkiraList();
      this._getTagList();
      this._getAnimeData(this.$route.params.id);
    },
    methods: {
      filesAdd () {
        this.formValidate.files.push({
          title: '',
          url: ''
        });
      },
      filesRemove (index) {
        this.formValidate.files.splice(index, 1);
      },
      //
      handleView (url) {
        this.imgName = url;
        this.visible = true;
      },
      handleRemove (file) {

        postImageDelete(this.formValidate.thumb).then((res) => {
          if (res.meta.errno === ERR_OK){
            //  upload 
            this.$Message.success('');
          }else{
            this.$Message.error(res.message);
          }
          const fileList = this.$refs.upload.fileList;
          this.$refs.upload.fileList.splice(fileList.indexOf(file), 1);
          this.formValidate.thumb = '';

        });


      },
      handleSuccess (res, file) {
        if (res.meta.errno === ERR_OK){
          this.formValidate.thumb = res.data;
          file.url = this.formValidate.thumb;
          this.$Message.success('');
        }else{
          this.$Message.error(res.message);
        }
      },
      handleFormatError (file) {
        this.$Notice.warning({
          title: '',
          desc: ' ' + file.name + '  jpg  png '
        });
      },
      handleMaxSize (file) {
        this.$Notice.warning({
          title: '',
          desc: ' ' + file.name + '  2M'
        });
      },
      handleBeforeUpload () {
        const check = this.uploadList.length < 5;
        if (!check) {
          this.$Notice.warning({
            title: ' 5 '
          });
        }
        return check;
      },
      //
      _getAnimeData(id) {
        getShowAnimeList(id).then(res => {
          if (res.meta.errno === ERR_OK){
            this.formValidate = res.data;
          }
        })
      },
      _getAkiraList () {
        getAkiraList().then((res) => {
          if (res.meta.errno === ERR_OK){
            this.akiraList= res.data
          }else{
            this.$Message.error(res.message);
          }

        })
      },
      _getTagList () {
        getTagList().then((res) => {
          if (res.meta.errno === ERR_OK){
            this.tagList= res.data
          }else{
            this.$Message.error(res.message);
          }

        })
      },
      handleSubmit (name) {
        let formData = this.formValidate;
        this.$refs[name].validate((valid) => {
          if (valid) {
            postUpdateAnimeList(formData).then((res) => {
              if (res.meta.errno === ERR_OK){
                this.formValidate.thumb = '';
                this.$Message.success('!');
                this.$router.push({
                  path: `/admin/anime`
                });
              }
            });

          } else {
            this.$Message.error('!');
          }
        })
      },
      handleReset (name) {
        this.$refs[name].resetFields();
      }
    },
    beforeDestroy() {
      this.formValidate = '';
    }
  }
</script>

<style>
  .ivu-upload.ivu-upload-drag{
    border: 0;
  }
  .demo-upload-list{
    display: inline-block;
    width: 80px;
    height: 80px;
    text-align: center;
    line-height: 80px;
    border: 1px solid transparent;
    border-radius: 4px;
    overflow: hidden;
    background: #fff;
    position: relative;
    box-shadow: 0 1px 1px rgba(0,0,0,.2);
    margin-right: 4px;
  }
  .demo-upload-list img{
    width: 100%;
    height: 100%;
  }
  .demo-upload-list-cover{
    display: none;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(0,0,0,.6);
  }
  .demo-upload-list:hover .demo-upload-list-cover{
    display: block;
  }
  .demo-upload-list-cover i{
    color: #fff;
    font-size: 20px;
    cursor: pointer;
    margin: 0 2px;
  }
</style>



